/**
 * @WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { createBlock, registerBlockType } from '@wordpress/blocks';
import {
	BlockControls,
	InspectorControls,
	BlockVerticalAlignmentToolbar,
	InnerBlocks,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, BaseControl, ToggleControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import example from './_example';
import deprecated from './deprecated';
import {
	ArkbProOnly,
	ArkbGapControls,
	ArkbMarginControl,
	ArkbDeviceTab,
	ArkbColumnWidthControl,
	ArkbColumnCountControl,
	LsSpaceControl,
	DynamicStyles,
	LsSelectorPreviewTip,
} from '@components';
import { getColumnBasis } from '@helper';
import { getDynamicStyles } from './_helper';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const DEFAULT_WIDTH = {
	pc: '400px',
	tab: '400px',
	mobile: '300px',
};

/**
 * リッチカラム
 */
const blockName = 'ark-block-columns';
registerBlockType(metadata.name, {
	title: __('Rich columns', 'arkhe-blocks'),
	icon: blockIcon,
	styles: [
		{ name: 'default', label: __('Default', 'arkhe-blocks'), isDefault: true },
		{ name: 'shadow', label: _x('Shadow', 'style', 'arkhe-blocks') },
	],
	example,
	transforms: {
		from: [
			//どのブロックタイプから変更できるようにするか
			{
				type: 'block',
				blocks: ['core/columns'],
				transform: (attributes, content) => {
					const newInnerBlocks = [];
					content.forEach((columnBlock) => {
						newInnerBlocks.push(
							createBlock('arkhe-blocks/column', {}, columnBlock.innerBlocks)
						);
					});
					return createBlock(metadata.name, {}, newInnerBlocks);
				},
			},
		],
	},
	edit: ({ clientId, attributes, setAttributes, isSelected }) => {
		const {
			vAlign,
			columnCount,
			columnWidth,
			gap,
			gapTab,
			gapMB,
			padding,
			paddingTab,
			paddingMB,
			isScrollable,
		} = attributes;

		let widthStyles = {};
		if (isScrollable) {
			widthStyles = {
				'--arkb-clmn-w--pc': columnWidth?.pc,
				'--arkb-clmn-w--tab': columnWidth?.tab,
				'--arkb-clmn-w--mb': columnWidth?.mobile,
			};
		} else {
			widthStyles = {
				'--arkb-clmn-w--pc': getColumnBasis(columnCount?.pc),
				'--arkb-clmn-w--tab': getColumnBasis(columnCount?.tab),
				'--arkb-clmn-w--mb': getColumnBasis(columnCount?.mobile),
			};
		}

		// インナーブロックが選択されているかどうか
		const isChildSelected = useSelect(
			(select) => select('core/block-editor').hasSelectedInnerBlock(clientId, false),
			[clientId]
		);

		const blockProps = useBlockProps({
			className: classnames(blockName, 'ark-has-guide'),
			style: {
				...widthStyles,
			},
			'data-valign': vAlign || null,
			'data-scrollable': isScrollable ? '1' : null,
		});

		// 左右marginの関係でカラムブロックは一つdivかませる
		const innerBlocksProps = useInnerBlocksProps(
			{
				className: `${blockName}__inner ark-has-appender`,
			},
			{
				allowedBlocks: ['arkhe-blocks/column'],
				template: [['arkhe-blocks/column'], ['arkhe-blocks/column']],
				templateLock: false,
				orientation: 'horizontal',
				// renderAppender: InnerBlocks.ButtonBlockAppender,
				renderAppender:
					isSelected || isChildSelected ? InnerBlocks.ButtonBlockAppender : false,
			}
		);

		const dynamicStyles = getDynamicStyles({
			gap,
			gapTab,
			gapMB,
			padding,
			paddingTab,
			paddingMB,
			clientId,
		});

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				<BlockControls group='block'>
					<BlockVerticalAlignmentToolbar
						onChange={(value) => {
							setAttributes({ vAlign: value });
						}}
						value={vAlign}
					/>
				</BlockControls>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')}>
						<ToggleControl
							label={__('Scroll horizontally', 'arkhe-blocks')}
							checked={isScrollable}
							onChange={(value) => {
								if (value) {
									setAttributes({
										isScrollable: true,
										columnCount: undefined,
										columnWidth: DEFAULT_WIDTH,
									});
								} else {
									setAttributes({ isScrollable: false, columnWidth: undefined });
								}
							}}
						/>
						{isScrollable && (
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Column Width', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<ArkbColumnWidthControl
									{...{ columnWidth, defaultWidth: DEFAULT_WIDTH, setAttributes }}
								/>
							</BaseControl>
						)}
						{!isScrollable && (
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Number of columns', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<ArkbColumnCountControl {...{ columnCount, setAttributes }} />
							</BaseControl>
						)}

						<ArkbProOnly>
							<BaseControl className='u-mb-25'>
								<BaseControl.VisualLabel>
									{__('Margin between columns', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<ArkbDeviceTab
									className='-gap -triple'
									controlPC={
										<ArkbGapControls
											value={gap}
											name='gap'
											setAttributes={IS_PRO ? setAttributes : null}
											defaultGap={{ x: '1.5rem', y: '1.5rem' }}
										/>
									}
									controlTab={
										<ArkbGapControls
											value={gapTab}
											name='gapTab'
											setAttributes={IS_PRO ? setAttributes : null}
											successionHelp={__('PC setting', 'arkhe-blocks')}
										/>
									}
									controlMB={
										<ArkbGapControls
											value={gapMB}
											name='gapMB'
											setAttributes={IS_PRO ? setAttributes : null}
											successionHelp={__('Tablet setting', 'arkhe-blocks')}
										/>
									}
								/>
							</BaseControl>
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Padding in cloumns', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<ArkbDeviceTab
									className='-padding -triple'
									controlPC={
										<LsSpaceControl
											name='padding'
											value={padding}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
										/>
									}
									controlTab={
										<LsSpaceControl
											name='paddingTab'
											value={paddingTab}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
											successionHelp={__('PC setting', 'arkhe-blocks')}
										/>
									}
									controlMB={
										<LsSpaceControl
											name='paddingMB'
											value={paddingMB}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
											successionHelp={__('Tablet setting', 'arkhe-blocks')}
										/>
									}
								/>
							</BaseControl>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				{/* ネガティブmarginの関係でカラムブロックは一つdivかませる */}
				<div {...blockProps}>
					{isScrollable && (
						<div className='arkb-scrollHint'>
							<span className='arkb-scrollHint__text'>
								{__('Scrollable', 'arkhe-blocks')}
								<svg
									className='arkb-scrollHint__svg'
									width='1em'
									height='1em'
									viewBox='0 0 32 32'
									role='img'
									focusable='false'
								>
									<path d='M30.4 16.664l-4.528-4.528-1.128 1.136 3.392 3.392h-26.536v1.6h28.8v-1.6z'></path>
								</svg>
							</span>
						</div>
					)}
					<LsSelectorPreviewTip
						icon={blockIcon}
						anchor={attributes.anchor}
						className={attributes.className}
					/>
					<div {...innerBlocksProps} />
				</div>
			</>
		);
	},

	save: ({ attributes }) => {
		const { vAlign, isScrollable } = attributes;

		const blockProps = useBlockProps.save({
			className: blockName,
			'data-valign': vAlign || null,
			'data-scrollable': isScrollable ? '1' : null,
		});

		return (
			<div {...blockProps}>
				<div className={`${blockName}__inner`}>
					<InnerBlocks.Content />
				</div>
			</div>
		);
	},
	deprecated,
});
