/**
 * @WordPress dependencies
 */
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { getColumnBasis } from '@helper';

/**
 * 変数
 */
const blockName = 'ark-block-columns';

/**
 * 数値と単位に分離する
 */
const getUnitNum = (val) => {
	if (!val) {
		return { num: 0, unit: 'rem' };
	}

	// 念のため、明示的に文字列へ変換。
	const valString = val.toString();
	const num = valString.replace(/[^0-9\.]/g, '');
	const unit = valString.replace(/[0-9\.]/g, '');
	return { num: parseFloat(num), unit };
};

export default [
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide', 'full'],
		},
		attributes: {
			vAlign: {
				type: 'string',
				default: '',
			},
			columnCount: {
				type: 'object',
			},
			columnWidth: {
				type: 'object',
			},
			gap: {
				type: 'object',
			},
			isScrollable: {
				type: 'boolean',
				default: false,
			},
		},
		save: ({ attributes }) => {
			const { vAlign, columnCount, columnWidth, gap, isScrollable } = attributes;
			let widthStyles = {};
			if (isScrollable) {
				// CSS削減を優先して、スクロールONの時は数値を常にセット
				widthStyles = {
					'--arkb-clmn-w--pc': columnWidth?.pc,
					'--arkb-clmn-w--tab': columnWidth?.tab,
					'--arkb-clmn-w--mb': columnWidth?.mobile,
				};
			} else {
				widthStyles = {
					'--arkb-clmn-w--pc': getColumnBasis(columnCount?.pc),
					'--arkb-clmn-w--tab': getColumnBasis(columnCount?.tab),
					'--arkb-clmn-w--mb': getColumnBasis(columnCount?.mobile),
				};
			}

			const blockProps = useBlockProps.save({
				className: blockName,
				style: {
					...widthStyles,
					'--arkb-gap--x': gap?.x,
					'--arkb-gap--y': gap?.y,
				},
				'data-valign': vAlign || null,
				'data-scrollable': isScrollable ? '1' : null,
			});

			return (
				<div {...blockProps}>
					<div className={`${blockName}__inner`}>
						<InnerBlocks.Content />
					</div>
				</div>
			);
		},
	},
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide', 'full'],
		},
		attributes: {
			vAlign: {
				type: 'string',
				default: '',
			},
			colPC: {
				type: 'string',
				default: '2',
			},
			colTab: {
				type: 'string',
				default: '2',
			},
			colMobile: {
				type: 'string',
				default: '1',
			},
			margin: {
				type: 'object',
				default: {
					x: '0.75rem',
					bottom: '1.5rem',
				},
			},
		},
		migrate: (attributes) => {
			const newAttrs = { ...attributes };

			newAttrs.columnCount = {
				pc: parseInt(attributes.colPC || 2),
				tab: parseInt(attributes.colTab || 2),
				mobile: parseInt(attributes.colMobile || 1),
			};

			if (!attributes.margin) {
				return newAttrs;
			}

			const oldGapX = attributes.margin.x;
			const gapY = attributes.margin.bottom;
			const { num, unit } = getUnitNum(oldGapX);
			const gapX = `${num * 2}${unit}`;

			newAttrs.gap = { x: gapX, y: gapY };
			return newAttrs;
		},
		save: ({ attributes }) => {
			const { vAlign, colPC, colTab, colMobile, margin } = attributes;
			const blockProps = useBlockProps.save({
				className: `${blockName} arkb-columns`,
				style: {
					'--arkb-fb': '1' !== colMobile ? getColumnBasis(colMobile) : null,
					'--arkb-fb_tab': '2' !== colTab ? getColumnBasis(colTab) : null,
					'--arkb-fb_pc': '2' !== colPC ? getColumnBasis(colPC) : null,
					'--arkb-clmn-mrgn--x': '0.75rem' !== margin.x ? margin.x : null,
					'--arkb-clmn-mrgn--bttm': '1.5rem' !== margin.bottom ? margin.bottom : null,
				},
				'data-valign': vAlign || null,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide', 'full'],
		},
		attributes: {
			vAlign: {
				type: 'string',
				default: '',
			},
			colPC: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col-pc',
				default: '2',
			},
			colTab: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col-tab',
				default: '2',
			},
			colMobile: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-columns',
				attribute: 'data-col',
				default: '1',
			},
		},
		migrate: (attributes) => {
			return {
				...attributes,
				margin: { x: '0.75rem', bottom: '1.5rem' },
			};
		},
		save: ({ attributes }) => {
			const basisSet = {
				col1: 100,
				col2: 50,
				col3: 33.33,
				col4: 25,
				col5: 20,
				col6: 16.66,
			};

			const { vAlign, colPC, colTab, colMobile } = attributes;
			const columnStyle = {
				'--arkb-fb': basisSet[`col${colMobile}`] + '%',
				'--arkb-fb_tab': basisSet[`col${colTab}`] + '%',
				'--arkb-fb_pc': basisSet[`col${colPC}`] + '%',
			};

			const blockProps = useBlockProps.save({
				className: `${blockName} arkb-columns`,
				style: columnStyle,
				'data-valign': vAlign || null,
				'data-col': colMobile,
				'data-col-tab': colTab,
				'data-col-pc': colPC,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
];
